.pagination-container {
    
    text-align: center;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;

    &__button {
        border: none;
        background-color: inherit;
        font-weight: 500;
        font-size: 12px;
        cursor: pointer;
        line-height: 16px;
        width: 40px;
        height: 40px;
        

        &--active {

            border-radius: 4px;
            color: white;
            background-color: #FF6B08;
        }
      
      }
      &__prev-button, &__next-button {

        width: 40px; 
        height: 40px; 
    border-radius: 4px; 
border: none; 
background-color: #F7F7F7; 
text-align: center;
cursor: pointer}
&__first-dot-button, &__last-dot-button {
    border: none; 
    width: 40px; 
    height: 40px;
    background: inherit;
    font-size: 12px;
    margin-right: 0px;
}
&__svg {
    margin-right: auto;
    margin-left: auto
}
}

    
@media screen and (min-width: 320px) and (max-width: 768px) {
    .pagination-container__first-dot-button {
        display: none;
    }
    .pagination-container__last-dot-button {
        display: none;
    }
}